import React, { useState, useEffect } from 'react';
import { createClient } from '@supabase/supabase-js';
import '../ThreeDPage/3DPage.css';

//  Supabase 
const supabase = createClient(
  'https://jvccejerkjfnkwtqumcd.supabase.co',
  'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Imp2Y2NlamVya2pmbmt3dHF1bWNkIiwicm9sZSI6ImFub24iLCJpYXQiOjE3NDU1MTMzMjAsImV4cCI6MjA2MTA4OTMyMH0.xgqIMs3r007pJIeV5P8y8kG4hRcFqrgXvkkdavRtVIw'
);

function MotionPage() {
  const [jobs, setJobs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchJobs = async () => {
      setLoading(true);
      setError(null);

      const { data: jobData, error: jobError } = await supabase
        .from('motion')
        .select('id, user_id, title, description, published_at, start_date, end_date, price, status, category')
        .eq('category', '');

      if (jobError) {
        console.error('Error fetching jobs from motion:', jobError);
        setError('  : ' + jobError.message);
        setLoading(false);
        return;
      }

      console.log('Fetched jobs from motion:', jobData);

      if (jobData.length === 0) {
        const { data: allCategories, error: categoryError } = await supabase
          .from('motion')
          .select('category');
        if (categoryError) {
          console.error('Error fetching categories:', categoryError);
        } else {
          console.log('Available categories in motion:', [...new Set(allCategories.map(job => job.category))]);
        }
      }

      const jobsWithCompany = await Promise.all(
        jobData.map(async (job) => {
          const { data: userData, error: userError } = await supabase
            .from('users')
            .select('username')
            .eq('id', job.user_id)
            .single();

          if (userError) {
            console.error('Error fetching user data for user_id', job.user_id, ':', userError);
            return { ...job, company: ' ' };
          }

          const startDate = new Date(job.start_date);
          const endDate = new Date(job.end_date);
          
          let deadlineDays = ' ';
          if (!isNaN(startDate.getTime()) && !isNaN(endDate.getTime())) {
            deadlineDays = Math.ceil((endDate - startDate) / (1000 * 60 * 60 * 24));
            deadlineDays = `${deadlineDays} `;
          }

          const publishedDateObj = new Date(job.published_at);
          const publishedDate = !isNaN(publishedDateObj.getTime())
            ? publishedDateObj.toLocaleDateString('ru-RU', {
                day: 'numeric',
                month: 'long',
                year: 'numeric'
              })
            : ' ';

          return {
            ...job,
            company: userData.username || ' ',
            salary: job.price ? `${job.price} ` : ' ',
            publishedDate,
            deadline: deadlineDays
          };
        })
      );

      setJobs(jobsWithCompany);
      setLoading(false);
    };

    fetchJobs();
  }, []);

  if (loading) {
    return <div className="loading">...</div>;
  }

  if (error) {
    return <div className="error">{error}</div>;
  }

  return (
    <div className="job-listings-container">
      {jobs.length === 0 ? (
        <p className="no-jobs">     .</p>
      ) : (
        jobs.map((job) => (
          <div key={job.id} className="job-card">
            <h3 className="job-title">{job.title}</h3>
            <p className="job-salary">{job.salary}</p>
            <p className="job-company">
              <span className="company-name">{job.company}</span>
              <span className="verified"></span>
            </p>
            <p className="job-deadline">
              <span className="deadline-label"> :</span> {job.deadline}
            </p>
            <p className="job-published">
              <span className="published-label"> :</span> {job.publishedDate}
            </p>
            <p className="job-description-label"> :</p>
            <p className="job-description">{job.description}</p>
            <button className="apply-btn"></button>
          </div>
        ))
      )}
    </div>
  );
}

export default MotionPage;
